import s from './Products.module.css'
import {useContext, useEffect, useState} from "react";
import {Context} from "../../../index";
import {observer} from "mobx-react-lite";
import SaleCard from "./SaleCard";
import React from 'react';
import {Button, Menu, MenuItem} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import {IProduct} from "../../../models/IStore";

export const Products = observer(()=> {
    const {store} = useContext(Context)
    const [menu, setMenu] = useState<boolean>(false);
    const [category, setCategory] = React.useState<string>("Категорії");
    const [loading, setLoading] = useState(true);
    const [filteredProducts, setFilteredProducts] = React.useState<IProduct[]>([]);
    const handleClick = () => {
        setMenu(!menu);
    };
    const handleClose = (cat:string) => {
        setMenu(false);
            setCategory(cat);
            const products = store.store.rowsAll.filter(product => product.category === cat);
            setFilteredProducts(products);
    };
    const handleAll = ()=>{
        setFilteredProducts(store.store.rowsAll)
        setMenu(false);
    }

    useEffect(() => {
        store.getStore("Автотовари")
            .then(()=>setFilteredProducts(store.store.rowsAll))
            .then(() => {
            setLoading(false);
        });
    }, []);
    if (loading) return <p>Завантаження...</p>;
    return (<div className={s.productsPage}>
        <div className={s.menu}>
            <Button
                onClick={handleClick}
                color={'inherit'}
            >
                <MenuIcon color={"inherit"}/> {category}
            </Button>
            <Menu
                open={menu}
            >
                <MenuItem onClick={handleAll} sx={{fontWeight: 'bold'}}>ВСІ ТОВАРИ</MenuItem>
                {store.store.menu.map((category, index) => <MenuItem
                    onClick={() => handleClose(category)} key={index}>{category}</MenuItem>)}
            </Menu>
        </div>
        <div className={s.products}>
            {filteredProducts.map((p, index) => <SaleCard key={index} image={p.image} alt={p.name}
                                                             description={p.description} head={p.name}
                                                             price={p.sellingPrice} button={'Купить'}/>)}
        </div>
    </div>)
})